  "has-validation-error.demo.hbs": "        {{this.errors}}\n\n        {{#if (has-validation-error this.errors)}}\n            <div local-class='HasError'>Has Error</div>\n        {{else}}\n            <div local-class='NoErrors'>No Errors</div>\n        {{/if}}\n\n        <OsfButton @onClick={{action (mut this.errors) (array true)}}>\n            No Errors (single)\n        </OsfButton>\n\n        <OsfButton @onClick={{action (mut this.errors) (array true true true)}}>\n            No Errors (multiple)\n        </OsfButton>\n\n        <OsfButton @onClick={{action (mut this.errors) (array 'Error')}}>\n            One Error\n        </OsfButton>\n\n        <OsfButton @onClick={{action (mut this.errors) (array 'Error1' 'Error2' 'Error3')}}>\n            All Errors\n        </OsfButton>\n\n        <OsfButton @onClick={{action (mut this.errors) (array true 'Error' true)}}>\n            Mixed\n        </OsfButton>\n\n        <OsfButton @onClick={{action (mut this.errors) (array)}}>\n            Empty\n        </OsfButton>",
