  "osf-button.action": "    @action\n    youPressedTheButton() {\n        if (this.buttonType === 'default') {\n            this.set('buttonType', 'primary');\n        } else if (this.buttonType === 'primary') {\n            this.set('buttonType', 'warning');\n        } else if (this.buttonType === 'warning') {\n            this.set('buttonType', 'danger');\n        } else {\n            this.set('buttonType', 'default');\n        }\n    }",
  "osf-button.demo.hbs": "        <OsfButton\n            @type={{this.buttonType}}\n            @onClick={{action this.youPressedTheButton}}\n        >\n            Do not press the button!\n        </OsfButton>",
  "osf-dialog.demo-big.hbs": "        <OsfDialog as |dialog|>\n            <dialog.trigger>\n                <OsfButton @onClick={{dialog.open}}>Big dialog!</OsfButton>\n            </dialog.trigger>\n            <dialog.heading>\n                The dialog expands with its contents to a point, then lets it scroll.\n            </dialog.heading>\n            <dialog.main>\n                {{#each (range 0 1000)}}\n                    Here's some stuff that's a whole bunch of stuff.\n                {{/each}}\n            </dialog.main>\n            <dialog.footer>\n                <OsfButton @type='success' @onClick={{dialog.close}}>\n                    Yep that's some stuff\n                </OsfButton>\n            </dialog.footer>\n        </OsfDialog>",
  "osf-dialog.demo-is-open.hbs": "        <OsfButton @onClick={{action (mut this.isOpen) true}}>\n            Open it!\n        </OsfButton>\n        <OsfDialog\n            @isOpen={{this.isOpen}}\n            @onClose={{action (mut this.isOpen) false}}\n            as |dialog|\n        >\n            <dialog.heading>\n                No need to depend on user action!\n            </dialog.heading>\n            <dialog.main>\n                This is a small dialog again.\n            </dialog.main>\n        </OsfDialog>",
  "osf-dialog.demo-little.hbs": "        <OsfDialog as |dialog|>\n            <dialog.trigger>\n                <OsfButton @onClick={{dialog.open}}>Little dialog!</OsfButton>\n            </dialog.trigger>\n            <dialog.heading>\n                Hello!\n            </dialog.heading>\n            <dialog.main>\n                Here's some stuff\n            </dialog.main>\n            <dialog.footer>\n                <OsfButton @type='primary' @onClick={{dialog.close}}>Cool</OsfButton>\n            </dialog.footer>\n        </OsfDialog>",
  "osf-dialog.demo-no-outside-click.hbs": "        <OsfDialog @closeOnOutsideClick={{false}} as |dialog|>\n            <dialog.trigger>\n                <OsfButton @onClick={{dialog.open}}>Harder to close dialog!</OsfButton>\n            </dialog.trigger>\n            <dialog.heading>\n                Clicking outside does nothing.\n            </dialog.heading>\n            <dialog.main>\n                Here's a long word:\n                Hellohellohellohellhellhellhellhellhellhellhellhellhellhellhellhellhellhellhellhellhellhellhellhellhellhellhelloooooooooooooooooooooooooohello\n            </dialog.main>\n            <dialog.footer>\n                <OsfButton @type='warning' @onClick={{dialog.close}}>\n                    Hello\n                </OsfButton>\n            </dialog.footer>\n        </OsfDialog>",
  "osf-dialog.demo-non-modal.hbs": "        <OsfDialog @isModal={{false}} as |dialog|>\n            <dialog.trigger>\n                <OsfButton @onClick={{dialog.open}}>\n                    Open!\n                </OsfButton>\n            </dialog.trigger>\n            <dialog.heading>\n                This one doesn't block the rest of the app.\n            </dialog.heading>\n            <dialog.main>\n                Click around in the background!\n            </dialog.main>\n        </OsfDialog>",
