  "subject-widget.demo.hbs": "        <Subjects::Manager\n            @model={{@model}}\n            @provider={{@model.provider}}\n            as |subjectsManager|\n        >\n            <Subjects::Widget\n                data-analytics-scope='Handbook'\n                @subjectsManager={{subjectsManager}}\n            />\n        </Subjects::Manager>",
